}
.btn_btn > .btn_ul > li > a > p {
  width: 100%;
  height: 0.8rem;
  line-height: initial;
  position: absolute;
  bottom: 1.4rem;
  color: #ffffff;
}
.btn_btn > .btn_ul > li:nth-of-type(2) {
  border-left: 4px solid #000;
  border-right: 4px solid #000;
}
.btn_btn > .btn_ul > li:nth-of-type(2) > a > i {
